<template>
	<div class="todo-item" v-bind:class="{ 'is-complete':todo.completed}">
		<input type="checkbox" @change="markCompleted">
		<p>{{ todo.title}}</p>
		<button @click="$emit('del-todo', todo.id)" class="del">x</button>
	</div>
</template>
<script>
	export default{
		name: "ToddoItem",
		props: ["todo"],

		methods:{
			markCompleted(){
				this.todo.completed = !this.todo.completed
			}
		}
	}
</script>
<style scoped>
	.todo-item{
		background: #f4f4f4;
		padding: 10px;
		border-bottom: 1px #ccc dotted;
	}

	.is-complete{
		text-decoration: line-through;
	}

	.del{
		background: #ff0000;
		color:#fff;
		border: none;
		padding: 5px 9px;
		border-radius: 50%;
		cursor: pointer;
		float:right;
	}

</style>

